.slider-wrapper {
  position: relative;
  height: 70vh;
  width: 98.5%;
  overflow: hidden;
}

.slide {
  height: 70vh;
  background-size: cover !important;
}

.slide::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
  bottom: 0;
  left: 0;
}

.previousButton,
.nextButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  background: url("https://www.ithurria.com/themes/ithurria2019/images/arrow-right-white.png")
    no-repeat center center / 16px;
  width: 72px;
  height: 72px;
  text-indent: -9999px;
  cursor: pointer;
}

.previousButton:hover,
.nextButton:hover {
  background: url("https://www.ithurria.com/themes/ithurria2019/images/arrow-right-white.png")
    no-repeat center center / 16px;
}

.previousButton {
  left: 0;
  transform: rotate(180deg) translateY(calc(50% + 0px));
}

.previousButton:hover {
  left: -10px;
}

.nextButton {
  right: 0;
}

.nextButton:hover {
  right: -10px;
}

.slider-content {
  text-align: center;
}

.slider-content .inner {
  padding: 0 70px;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-content .inner button {
  transition-delay: 0s;
}

.slider-content .inner h1 {
